export const deviceInfo = () => {
  try {
    const device = window.navigator.userAgent;
    console.log("uesrAgent", window.navigator.userAgent);

    if (device.indexOf("iPad") > -1) {
      // ipad
      return {
        isLaptop: true,
      };
    } else if (device.indexOf("Android") > -1 || device.indexOf("ios") > -1) {
      // 
      return {
        isMobile: true,
      };
    } else {
      // 
      return {
        isPC: true,
      };
    }
  } catch (error) {
    return {};
  }
};
